import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { MyPropertyComponent } from './my-property.component';
import { PropertyConfigurationComponent } from './components/property-configuration/property-configuration.component';
import { PropertyDetailsComponent } from './components/property-configuration/property-details/property-details.component';
import { PropertyProfileComponent } from './components/property-configuration/property-profile/property-profile.component';
import { PropertyFacilityComponent } from './components/property-configuration/property-facility/property-facility.component';
import { PropertyParkingDetailsComponent } from './components/property-configuration/property-parking-details/property-parking-details.component';
import { PropertyAssetCategoryComponent } from './components/property-configuration/property-asset-category/property-asset-category.component';
import { PropertyAlertsComponent } from './components/property-alerts/property-alerts.component';
import { PropertyTowerInfoComponent } from './components/property-configuration/property-details/property-tower-info/property-tower-info.component';
import { PropertyTowerUnitInfoComponent } from './components/property-configuration/property-details/property-tower-info/property-tower-unit-info/property-tower-unit-info.component';
import { PropertyTowerFacilityComponent } from './components/property-configuration/property-facility/property-tower-facility/property-tower-facility.component';
import { PropertyAssetCategoryInfoComponent } from './components/property-configuration/property-asset-category/property-asset-category-info/property-asset-category-info.component';
import { PropertyAlertsInfoComponent } from './components/property-alerts/property-alerts-info/property-alerts-info.component';
import { PropertyParkingDetailsInfoComponent } from './components/property-configuration/property-parking-details/property-parking-details-info/property-parking-details-info.component';


export const routes: Routes = [

	{ path: '', redirectTo: 'property-configuration', pathMatch: 'full'},
	{ path: 'property-configuration', component: PropertyConfigurationComponent, canActivate: [AuthGuard],
		children: [
			{ path: '', redirectTo: 'profile', pathMatch: 'full'},
			{ path: 'profile', component: PropertyProfileComponent },
			{ path: 'details', component: PropertyDetailsComponent },
			{ path: 'facility', component: PropertyFacilityComponent },
			{ path: 'parking', component: PropertyParkingDetailsComponent },
			{ path: 'asset/:id', component: PropertyAssetCategoryComponent },
			{ path: '**', redirectTo: 'profile', pathMatch: 'full'}
		],
	},
	{ path: 'property-alerts', component: PropertyAlertsComponent, canActivate: [AuthGuard],
	children: [
			{ path: '', redirectTo: 'all', pathMatch: 'full'},
			{ path: 'all', component: PropertyAlertsInfoComponent },
			{ path: 'alert/:id', component: PropertyAlertsInfoComponent },
			{ path: '**', redirectTo: 'all', pathMatch: 'full'}
		]
	},
	{ path: '**', redirectTo: 'property-configuration', pathMatch: 'full'}
	
		
	];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MyPropertyRoutingModule { }
