import { Component, OnInit } from '@angular/core';

import { ApartmentService } from '../../../../../api/services/apartment.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { LookupService } from '../../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-property-parking-details',
  templateUrl: './property-parking-details.component.html',
  styleUrls: ['./property-parking-details.component.scss']
})
export class PropertyParkingDetailsComponent implements OnInit {

  isDataLoaded:boolean = false;
  propertyTowerData:any;

  unitsLength:any;

  constructor(
  	private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  ngOnInit() {

  	this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  		this.propertyTowerData = res;
  		this.isDataLoaded = true;

  	});

  }

}
