import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { AssetService } from '../../../../../../api/services/asset.service';
import { SharedService } from '../../../../../../shared/services/shared.service';
import { LookupService } from '../../../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-property-asset-category-info',
  templateUrl: './property-asset-category-info.component.html',
  styleUrls: ['./property-asset-category-info.component.scss']
})
export class PropertyAssetCategoryInfoComponent implements OnInit {
  
  @Input() tower:any;

  assetCategoryId:any;

  assetDataList:any;

  isDataLoaded:boolean = false;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
  	private assetService:AssetService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  ngOnInit() {

  	this.assetCategoryId = this.route.params['value'].id;

  	console.log(this.assetCategoryId);
  	
  	this.assetService.GetAssetCountsByCategoryId(this.assetCategoryId).subscribe((res:any) => {
  		console.log(res);
  		this.assetDataList = res;
  		this.isDataLoaded = true;
  	})


  }

}
