import { Component, OnInit, AfterViewInit, HostListener  } from '@angular/core';
import { OwlOptions } from 'ngx-owl-carousel-o';

import { AssetService } from '../../../../api/services/asset.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-property-alerts',
  templateUrl: './property-alerts.component.html',
  styleUrls: ['./property-alerts.component.scss']
})
export class PropertyAlertsComponent implements OnInit, AfterViewInit {

  itemPositions:any = [];
  currentPosition:any = 0;

  totalTabItems:any;
  
  tab:any;
  tabItems:any;

  selectedTab:any = 0;

  alertData:any;
  
  customOptions: OwlOptions = {
    loop: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    margin:15,
    dots: true,
    navSpeed: 700,
    navText: ['', ''],
    responsive: {
      0: {
        items: 2
      },
      400: {
        items: 2
      },
      740: {
        items: 4
      },
      940: {
        items: 5
      }
    },
    nav: false
  }

  alertTypes:any;

  isDataLoaded:boolean = false;

  constructor(
    private assetService:AssetService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  moveTabRight(){
  	if (this.currentPosition + 1 < this.itemPositions.length) {
      document.getElementById("alerts-tabs").style.left = "-" + this.itemPositions[++this.currentPosition] + "px";
  	}
    
  }

  moveTabLeft(){
    if (this.currentPosition - 1 >= 0) {
      document.getElementById("alerts-tabs").style.left = "-" + this.itemPositions[--this.currentPosition] + "px";
    }
  }

  widthOfList() {

    this.itemPositions = [];
  	var marginWidth = 60;

  	var wrapperWidth = document.querySelector('#alerts-wrapper').clientWidth - marginWidth;

  	var itemsWidth = 0;
  	this.itemPositions.push(0);

  	_.each(this.tabItems, (item)=> {
  		var itemWidth = item.offsetWidth;
  	    itemsWidth += itemWidth;
  	    if (itemsWidth > wrapperWidth) {
  	      this.itemPositions.push( itemsWidth - wrapperWidth ); 
  	    }
  	});

  	return itemsWidth;

  }


  isMobileView() {
    return window.innerWidth <= 576 ? true : false;
  }

  formatName(name){
  	return name.replace(/-/g, ' ');
  }

  getAssetType(type, item){
  	
    this.selectedTab = type;
    this.alertData = item;

    if(item != 'nill'){
      this.alertData = item;
    }

  }

  isTabActive(type){
  	return this.selectedTab == type ? 'active' : '';
  }

  @HostListener('window:resize', ['$event'])
  onWindowResize(event) {
    this.widthOfList();
    if(this.isMobileView()){
      document.getElementById("alerts-tabs").style.left = 0 + 'px';
      this.currentPosition = 0;
    }
  }

  ngOnInit() {

  	this.alertTypes = [
  		{
	  		'label':'Maintenance in 30 days',
	  		'type':'MaintenanceInXDays',
	  		'count':30
  		},
  		{
	  		'label':'Maintenance Expired',
	  		'type':'MaintenanceExpired',
	  		'count':0
  		},
  		{
	  		'label':'Maintenance Completed',
	  		'type':'MaintenanceCompleted',
	  		'count':0
  		},
  		{
	  		'label':'Insurance Expiring in 30 days',
	  		'type':'InsuranceExpiringInXDays',
	  		'count':30
  		},
  		{
	  		'label':'Expired Insurance',
	  		'type':'ExpiredInsurance',
	  		'count':0
  		}
  	]

  	_.each(this.alertTypes, (item, index) => {

  		var params = {
  			ApartmentId: parseInt(this.cookieService.get('apartmentId')),
  			InputType: item.type,
  			days: item.count
  		}

  		this.assetService.GetAssetMasterDetail(params).subscribe((res:any) => {
	  		if(res == undefined || res.length == 0){
	  			item.assetCount = 0
	  		}
	  		else {
	  			item.assetCount = res.length;
	  		}

        if(index == this.alertTypes.length-1){
          this.isDataLoaded = true;
        }

        if(index == 0){
          this.alertData = item;
        }

  		})

  	})

  }

  ngAfterViewInit() {

  	setTimeout(() => {

        this.tab = document.getElementById('alerts-tabs');
        this.tabItems = this.tab.getElementsByTagName("li");
        this.totalTabItems = this.tabItems.length;

        this.widthOfList();

    }, 1000);

  }


}
