import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';


import { AssetService } from '../../../../../api/services/asset.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-property-alerts-info',
  templateUrl: './property-alerts-info.component.html',
  styleUrls: ['./property-alerts-info.component.scss']
})
export class PropertyAlertsInfoComponent implements OnInit {
  
  @Input() alertData:any;
  alerts:any;

  isDataLoaded:boolean = false;

  isNoItemsAvailable:boolean = false;

  constructor(
  	private assetService:AssetService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getDate(date){
  	return moment(date).format("DD/MM/YYYY");
  }

  ngOnInit() {

  	

  }

  ngOnChanges(changes: SimpleChanges) {

  	if(this.alertData != undefined){

  		this.isDataLoaded = false;

	  	var params = {
		    ApartmentId: parseInt(this.cookieService.get('apartmentId')),
		    InputType: this.alertData.type,
		    days: this.alertData.count
	    }

		this.assetService.GetAssetMasterDetail(params).subscribe((res:any) => {

			if(res == undefined || res.length == 0){
			  this.isNoItemsAvailable = true;
			}
			else {
			  this.alerts = res;
			  this.isNoItemsAvailable = false;
			}

			this.isDataLoaded = true;

		},
		error => {
			this.isDataLoaded = true;
			this.isNoItemsAvailable = true;
		});

  	}

  }

}
