import { Component, OnInit, ChangeDetectorRef } from '@angular/core';

import { ApartmentService } from '../../../../../api/services/apartment.service';
import { FacilityService } from '../../../../../api/services/facility.service';
import { LookupService } from '../../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-property-facility',
  templateUrl: './property-facility.component.html',
  styleUrls: ['./property-facility.component.scss']
})
export class PropertyFacilityComponent implements OnInit {
  
  isDataLoaded:boolean = false;
  propertyTowerData:any;
  facilityData:any;

  dotItems = ['high','medium','low','blue','cyan'];

  constructor(
    private cdRef:ChangeDetectorRef,
  	private apartmentService:ApartmentService,
    private facilityService:FacilityService,
    private lookupService: LookupService,
    private cookieService:CookieService
  ) { }

  isFacilityEmpty(){
    return this.facilityData.length == 0
  }

  ngOnInit() {


  	this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  		this.propertyTowerData = res;

      this.facilityService.GetPCApartmentFacility(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

          this.facilityData = res;

          _.each(this.facilityData, (item, index) => {
            this.facilityData[index].class = this.dotItems[Math.floor(Math.random() * this.dotItems.length)];
          });

          this.isDataLoaded = true;

      });

  	});

  }

}
